.container {
  text-align: center;
  padding: {
    bottom: 10px;
  }

  .input-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 320px;
    padding: {
      top: 40px;
      bottom: 40px;
    }

    .input--text {
      outline: none;
      border-radius: 30px;
      width: 90vw;
      max-width: 625px;
      min-width: 230px;
      height: 50px;
      border: 1px solid #221718;
      font-size: 16px;
      line-height: 16px;
      transform: translateX(45px);
      box-shadow: 3px 3px 1px gray;
      padding: {
        top: 3px;
        left: 20px;
        right: 110px;
      }
    }

    .input--file {
      display: flex;
      align-items: center;
      justify-content: center;
      border: 2px solid #221718;
      border-radius: 50%;
      overflow: hidden;
      width: 40px;
      min-width: 40px;
      height: 40px;
      transform: translateX(-45px);
      cursor: pointer;
      margin: {
        right: 5px;
      }
    }

    .input--submit {
      background-color: white;
      display: flex;
      align-items: center;
      justify-content: center;
      border: 2px solid #221718;
      border-radius: 50%;
      width: 40px;
      min-width: 40px;
      height: 40px;
      transform: translateX(-45px);
      cursor: pointer;
    }

    .btn--delete {
      background-color: white;
      z-index: 3;
      display: flex;
      align-items: center;
      justify-content: center;
      border: 2px solid #221718;
      color: #221718;
      border-radius: 50%;
      overflow: hidden;
      width: 40px;
      height: 40px;
      transform: translateX(-45px);
      cursor: pointer;
      margin: {
        right: 5px;
      }
    }

    .btn:hover {
      background-color: #e9e9e9;
    }
  }

  .preview-img {
    z-index: 3;
    max-width: 80vw;
    max-height: 20vh;
    box-shadow: 3px 3px 1px gray;
    border: 1.5px solid #221718;
    background-color: white;
  }
}
